@import (less) 'color';
.row.nbp-progress-container {
    background-color: @lightest_gray;
    margin-left: -25px;
    margin-right: -25px;
    border-bottom: 1px solid @light_gray;
    border-top: 1px solid @light_gray;
}

.row.nbp-progress-container.sticky {
    position: fixed !important;
    top:0;
    width:100% !important;
    z-index: 9999 !important;
}

row.nbp-progress-container.sticky > .progress-container{

}
.progress-container {
    display: table;
}

.progress-steps-container {
    height: 5.000em;
    display: table-cell;
    vertical-align: middle;
}

.progress {
    height: 0.875em;
    background-color: @lightest_gray;
    border-radius: 0.875em;
    border: 0.125em solid @light_gray;
    margin-bottom: 0.250em;
    box-shadow: none;
    overflow: hidden;
}

.progress.active {
    border: 0.125em solid @green;
}

.progress-bar {
    border-radius: 0.875em;
    background-color: @green;
    box-shadow: none;
}

.progress-step-name {
    position: relative;
    text-align: center;
    font-size: 0.625em;
    font-weight: bold;
    text-transform: uppercase;
    color: @gray;
}

.progress-step-name.arrowStep:after {
    position: absolute;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    content: " ";
    width: 15px;
    height: 15px;
    border: 0 solid @light_gray;
    border-width: 1px 0 0 1px;
    background: #fff;
    left: 0;
    right: 0;
    bottom: -32px;
    margin: 0 auto;
}

.progress-step-name.active {
    color: @green;
}

.contrasto-on {
    .progress {
        background-color: @white;
        border: 0.125em solid @black;
    }
    .progress.active {
        border: 0.125em solid @black;
    }
    .progress-bar {
        background-color: @black;
    }
    .progress-step-name {
        color: @black;
    }
    .progress-step-name.arrowStep:after {
        transform: rotate(45deg);
        content: " ";
        width: 15px;
        height: 15px;
        border: 0 solid @black;
        border-width: 1px 0 0 1px;
        background: #fff;
        left: 0;
        right: 0;
        bottom: -32px;
        margin: 0 auto;
    }
    .progress-step-name.active {
        color: @black;
    }
}